.contact{
    background-color: white;
    display: flex;

    .left{
        flex: 1;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
        }
    }
    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 5%;
        justify-content: space-around;

        h2{
            font-size: 30px;
        }

        form{
            width: 70%;
            height: 60%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;
             }

            textarea{
                width: 300px;
                height: 270px;
                font-size: 14px;
            }

            button{
               width: 150px;
               height: 30px;
               color: white;
               background-color: crimson;
               border: none;
               border-radius: 10px;
               font-weight: 500;
               cursor: pointer;

                &:focus{
                    outline: none;
                }

            }
            span{
                color:green;
            }
        }
    }
}